#scroll-to-top {
    display: inline-block;
    background-color: #FF9800;
    font-size: 3rem;
    width: 50px;
    height: 50px;
    text-align: center;
    border-radius: 4px;
    position: fixed;
    bottom: 30px;
    right: 30px;
    transition: background-color .3s, opacity .5s, visibility .5s;
    opacity: 0;
    visibility: hidden;
    z-index: 1000;
    cursor: pointer;
}

#scroll-to-top.show {
    opacity: .6;
    visibility: visible;
}